import moment from 'moment'

export const getDateByNum = (num,symbol) =>{
    const currentDate = new Date();
//今天时间
    let time1 = currentDate.getFullYear() + "-" + (currentDate.getMonth() + 1) + "-" + currentDate.getDate()
    console.log(time1);
    let date2 = new Date(currentDate);
    date2.setDate(currentDate.getDate() + num);
//num是正数表示之后的时间，num负数表示之前的时间，0表示今天
    let time2 = date2.getFullYear() + symbol + (date2.getMonth() + 1) + symbol + date2.getDate();
    console.log(time2);
    return time2;
}

