export const formatYYMMDD = (date) => {
    return moment(new Date(date)).format("YYYY-MM-DD")
}

export const formatYYMMDDHHMMSS = (date) => {
    return moment(new Date(date)).format("YYYY-MM-DD hh:mm:ss")
}

export const formatYYMMDDHHMM = (date) => {
    return moment(new Date(date)).format("YYYY-MM-DD hh:mm")
}

// 分钟数转换为 小时：分钟
export const formatMMTOHHMM = (str) => {
    if (str !== "0" && str !== "" && str !== null) {
        return ((Math.floor(str / 60)).toString().length < 2 ? "0" + (Math.floor(str / 60)).toString() :
            (Math.floor(str / 60)).toString()) + ":" + ((str % 60).toString().length < 2 ? "0" + (str % 60).toString() : (str % 60).toString());
    }
    else
    {
        return "";
    }
}

// 小时分钟格式转换为分钟数
export const formatHHMMToMM =(str) =>
{
    var arrminutes = str.split(":");
    if (arrminutes.length == 2) {
        var minutes = parseInt(arrminutes[0]) * 60 + parseInt(arrminutes[1]);
        return minutes;
    }
    else {
        return 0;
    }
}

// 通过value值获取对应的运动类型名称
export const getSportObj= (sportList) => {
    const Obj = {}
    sportList.map(item => {
        const childs = item.children
        childs.map(childItem => {
            Obj[`${childItem['value'].toString()}`] = childItem['label']
        })
    })
    return Obj
}

export const handleDataMinMaxAverage = (arr,type) => {
    if(arr.length === 0) return {
        max: 0,
        min: 0,
        average: 0
    }
    let count = type === "day" ? arr.length : type === "week" ? 7 : type === "month" ? 30 : 365
    let heightMax = Math.max.apply(null,arr)
    let heightMin = Math.min.apply(null,arr);
    let heightAverage = arr.reduce((num, item, index) => {
        if(index != arr.length-1) {
            return num+item;
        } else {
            return (num+item)/count;
        }
    },0);
    return {
        max: heightMax,
        min: heightMin,
        average: heightAverage.toFixed(2)
    }
}

