<template>
  <div class="yeardetial">
    <sHeader :name= "title"  />
    <van-collapse v-for="(item, index) in yearList" :key="index" v-model="item.active" accordion>
      <van-collapse-item  :title="item['date']" :name="1" >
        <div class="list_record"  v-for="(childItem, childIndex) in item.child" :key="childIndex">
          <div> <span>{{ childItem['measureTimeDesc'] }}</span> <span>心率（{{ childItem['heartRate']}} 次/分 ）</span> </div>
        </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import {onMounted, reactive, toRefs} from "vue";
import sHeader from '@/components/SimpleHeader'
import { useRoute } from "vue-router";
import {formatMMTOHHMM} from "@/utils/util";
import {useStore} from "vuex";

export default {
  name: "YearDetialList",
  components:{
    sHeader
  },
  setup(){
    const router = useRoute()
    const store = useStore()
    /**
     * 声明
     * @type {UnwrapNestedRefs<{list: []}>} 数据列表
     */
    const state = reactive({
      yearList:[],
      params: {},
      title:""
    })

    onMounted(()=>{
      const { query: { listJson, date } } = router
      const item = JSON.parse(listJson)
      console.log("年数据",item)

      state.title = date
      state.yearList = item
    })

    return {
      ...toRefs(state)
    }
  }
}
</script>

